import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { AddSystemService } from 'src/app/shared/services/add-system.service';
import { ChargeMaintenanceService } from 'src/app/shared/services/charge-maintenance-service.service';
import { RoleService } from 'src/app/shared/services/role.service';

@Component({
  selector: 'npr-incoming',
  templateUrl: './incoming.component.html',
  styleUrls: ['./incoming.component.scss']
})
export class IncomingComponent implements OnInit {
  isLoading:boolean = false;
  respArray: any[] = [];
  roleCodes = new permissionsLabels();
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  currentUser: string;
  statusArr: String[] = ['Authorized', 'Unauthorized', 'Open', 'Close'];
  link: string;
  constructor( private roleService: RoleService,private apiService: AddSystemService,private router:Router,
    private cdr:ChangeDetectorRef) {}

  ngOnInit(): void {
    this.link = localStorage.getItem("link");
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    setTimeout(() => {
      console.log(this.roleCodes);
    }, 3000);
    this.currentUser = localStorage.getItem("userFromLogin");
  
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength : 5,
      columnDefs: [ { type: 'date', 'targets': [4] } ],
      order: [[4, 'desc']],
      processing: true,
      // lengthMenu: [[5, 10, 20, -1], [5, 10, 20, 30]],
      lengthMenu: [
        [5, 10, 20, 30,-1],
        [5, 10, 20, 30,"ALL"],
      ],
      retrieve:true
    };

    this.getChargeMaintainenceSummary();

  }
  newRolePermissions()
  {

    this.roleService.fetchScreenPermissions('Charge Maintenance');
  }
  getChargeMaintainenceSummary() {
    this.isLoading = true;
    this.apiService.getIncomingOutgoingSummary(this.link).subscribe((dataresp) => {
      console.log(dataresp);
      this.respArray = dataresp;
      this.isLoading = false;

      this.dtTrigger.next();
    });
  }
  edit(data){
    this.apiService.setReference({
      referenceId: data.referenceNo,
    });
    localStorage.setItem('idValue',data.id);
    this.router.navigateByUrl("/rule-mapping/conversion");
  }

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }

check(data){
  console.log(data);
}
refreshPage() {
  window.location.reload();
 }
}